import React, {Component} from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCoffee } from '@fortawesome/free-solid-svg-icons';
import {padding, font, colors, iconSize, dimensions} from '../styles/base';
import renderIf from './conditionalRender';
import {IoMdHome, IoIosChatbubbles, IoMdAnalytics, IoIosPaper} from 'react-icons/io'
import '../../App.css'


export default class ComponentName extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    const {title, iconName} = this.props;
    return (
      <div style={navContainer}>

      <div style={iconBackground}>
            {renderIf(
              iconName === 'IoMdHome',
              <IoMdHome style={icon} color={colors.white}  size={dimensions.width * 0.05} />,
            )}
            {renderIf(
              iconName === 'IoMdAnalytics',
              <IoMdAnalytics style={icon} color={colors.white}  size={dimensions.width * 0.05} />,
            )}
            {renderIf(
              iconName === 'IoIosPaper',
              <IoIosPaper style={icon} color={colors.white}  size={dimensions.width * 0.05} />,
            )}
            {renderIf(
              iconName === 'IoIosChatbubbles',
              <IoIosChatbubbles style={icon} color={colors.white} size={dimensions.width * 0.05} />,
            )}
          
        </div>
          <p style={titleStyle}>{title}</p>
      </div>
    );
  }
}

const navContainer = {
  padding: padding.sm,
  alignSelf: 'center',
  textAlign: 'center',
  justifyContent: 'center',
  display: 'block',
  borderRadius: padding.md,
};
const iconBackground = {
  background: colors.background,
  display: 'inline-block',
  alignItems: 'center',
  alignSelf: 'center',
  borderRadius: padding.md,
  padding: padding.md,
}
const icon = {
  alignSelf: 'center',
}
const titleStyle = {
  fontSize: font.xsm,
  alignSelf: 'center',
  display: 'block',
  color: colors.black
}
