/* eslint-disable react/no-did-mount-set-state */
import React, {Component} from 'react';
import '../../../App.css';
import {dimensions, colors, padding, font} from '../../styles/base';
import NavLocation from '../../components/NavLocation';
import CountDown from '../../components/countdown';

export default class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      minutes: null,
      seconds: null,
      daysleft: null,
      hoursLeft: null,
    };
  }
  componentDidMount = async () => {
    const firstDate = new Date();
    const secondDate = (new Date('Fri Apr 30 2020 18:30:00 GMT+0100 (West Africa Standard Time)'))

    // get total seconds between the times
    var delta = Math.abs(secondDate - firstDate) / 1000;

    // calculate (and subtract) whole days
    var days = Math.floor(delta / 86400);
    delta -= days * 86400;

    // calculate (and subtract) whole hours
    var hours = Math.floor(delta / 3600) % 24;
    delta -= hours * 3600;

    // calculate (and subtract) whole minutes
    var minutes = Math.floor(delta / 60) % 60;
    delta -= minutes * 60;

    // what's left is seconds
    var seconds = delta % 60;  // in theory the modulus is not required
    console.log(days + ' : ' + hours  + ' : ' + minutes + ' : ' + seconds)
  };
  render() {
    return (
      <div style={container}>
        <div style={topContent}>
        <header style={header}>WELCOME TO BROADWAY MONEY</header>
        <p style={paragraph}>This project is currently being developed by AppDex we appreciate your efforts towards the success of this project,
            it is important to us that you report any bugs or abnormality through by dropping message in the testers review.
              for more information about the project kindly check out the project GUI to understand specifications.
         </p>
        </div>
        <div style={navigation}>
          <div><NavLocation title='PROJECT HOME' iconName='IoMdHome' /></div>
          <div><NavLocation title='ADMIN HOME' iconName='IoMdAnalytics' /></div>
          <div><NavLocation title='PROJECT GUI' iconName='IoIosPaper' /></div>
          <div><NavLocation title='TESTER REVIEW' iconName='IoIosChatbubbles' /></div>
        </div>

        <div style={countDownBody}>
          <CountDown minutesleft={this.state.minutes} secondsLeft={this.state.seconds}  />
          <CountDown minutesleft={this.state.minutes} secondsLeft={this.state.seconds}  />
        </div>
      </div>
    );
  }
}
