import React,{Component} from 'react';
import {Button} from "reactstrap";
import {dimensions, colors} from '../styles/base';
import { FaRegWindowRestore } from "react-icons/fa";
import { MdQuestionAnswer } from "react-icons/md";
import { IoIosPeople, IoIosArrowDropleftCircle, IoIosArrowDroprightCircle } from 'react-icons/io';
import { quizData } from '../data/quiz-data';


export default class Quiz extends Component{
    constructor(props) {
        super(props);
        this.state = {
            currentQuestion: 0,
            myAnswer: null,
            options: [],
            score: 0,
            disabled: true,
            isEnd: false,
            answer: null
          };
      }
      loadQuizData = () => {
        // console.log(quizData[0].question)
        this.setState(() => {
          return {
            questions: quizData[this.state.currentQuestion].question,
            answer: quizData[this.state.currentQuestion].answer,
            options: quizData[this.state.currentQuestion].options,
            id: quizData[this.state.currentQuestion].id
          };
        });
      };
      async componentDidMount() {
        await this.loadQuizData();
      };
      nextQuestionHandler = () => {
        // console.log('test')
        if (this.state.id < 3) {
        this.setState({
            answer: quizData[this.state.id + 1].answer,
            currentQuestion: this.state.currentQuestion + 1,
            id: this.state.id + 1,
        });
        //console.log(this.state.answer)
        //console.log(this.state.currentQuestion);
      };
        }
      prevQuestionHandler = () => {
        if (this.state.id > 0) {
                this.setState({
                answer: quizData[this.state.id - 1].answer,
                currentQuestion: this.state.currentQuestion - 1,
                id: this.state.id - 1,
            });
        }
      };
      MythButton = () => {
        var that = this;
        const { answer, score } = this.state;
        if ('Myth' === answer) {
          this.setState({
            score: score + 1
          });
          alert('Correct Answer')
        }
      }
      FactButton = () => {
        const { answer, score } = this.state;
        if ('Fact' === answer) {
            this.setState({
              score: score + 1
            });
            alert('Correct Answer')
          }
      }
    
      componentDidUpdate(prevProps, prevState) {
        if (this.state.currentQuestion !== prevState.currentQuestion) {
          this.setState(() => {
            return {
              disabled: true,
              questions: quizData[this.state.currentQuestion].question,
              options: quizData[this.state.currentQuestion].options,
              answer: quizData[this.state.currentQuestion].answer
            };
          });
        }
      }
      //check answer
      checkAnswer = answer => {
        this.setState({ myAnswer: answer, disabled: false });
      };
      finishHandler = () => {
        if (this.state.currentQuestion === quizData.length - 1) {
          this.setState({
            isEnd: true
          });
        }
      };
    render() {
        const { options, myAnswer, currentQuestion, isEnd } = this.state;
        if (isEnd) {
            return (
              <div className="result">
                <h3>Game Over your Final score is {this.state.score} points </h3>
                <p>
                  The correct answer's for the questions was
                  <ul>
                    {quizData.map((item, index) => (
                      <li className="ui floating message options" key={index}>
                        {item.answer}
                      </li>
                    ))}
                  </ul>
                </p>
              </div>
            );
          } else {
            return (
            <div style={container}>
                <div style={content}>
                    <div style={title}>
                        <p style={titleText}>How good are you at marketing ?</p>
                        <p style={text}>Play fun quiz games on Broadway Money and increase you marketing knowledge, you can
                        also get quiz result and corresponding answers sent to your e-mail.</p>
                    </div>

                    <div style={gameContent}>
                        <div style={displayscreen}>
                            <div style={{width: '85%', height: '100%', display: 'flex', textAlign: 'center', flexDirection: 'column', alignItems: 'center', justifyContent: 'space-between', paddingTop: 20, paddingBottom: 40}}>
                                <div style={questionAnswered}>
                                    <p style={{
                                        padding: 0,
                                        margin: 0,
                                        fontFamily: 'Soleil,Arial,Sans-Serif',
                                    }}>{currentQuestion} / {quizData.length - 1}</p>
                                </div>
                                <div style={navigatorButton}>
                                    <button onClick={this.prevQuestionHandler}  style={icon}>
                                        <IoIosArrowDropleftCircle color={'#3E3E42'}  size={30}  />
                                    </button>
                                    <button onClick={this.nextQuestionHandler}  style={icon}>
                                        <IoIosArrowDroprightCircle color={'#3E3E42'}  size={30}  />
                                    </button>
                                </div>
                                <div style={{
                                    flex: 1,
                                    display: 'flex',
                                    flexDirection: 'column'
                                }}>
                                    <div style={questionType}>
                                        <h4 style={{
                                            fontSize: 16,
                                            fontFamily: 'Soleil,Arial,Sans-Serif',
                                            fontWeight: 'bold',
                                        }}>Myth or Fact?</h4>
                                    </div>
                                    <p style={question}>{this.state.questions} </p>
                                </div>
                                <div style={optionButton}>
                                    <Button onClick={this.MythButton} color="primary" style={{background: '#0081FF', border: '1px solid #0081FF'}} size="sm" type="button">
                                    Myth
                                    </Button>

                                    <Button onClick={this.FactButton} color="danger" style={{background: '#FB4A46', border: '1px solid #FB4A46'}} size="sm" type="button">
                                    Fact
                                    </Button>
                                </div>
                            </div>
                        </div>
                        <div style={tabcontrol}>
                                    <div style={tabOne}>
                                        <FaRegWindowRestore style={tabIcon} color={'#3E3E42'}  size={35} />
                                        <p style={tabTitle}>Social Media Marketing</p>
                                    </div>
                                    <div style={tabTwo}>
                                        <MdQuestionAnswer style={tabIcon} color={'#fff'}  size={35} />
                                        <p style={{
                                            fontSize: 12,
                                            color: '#fff',
                                            padding: 0,
                                            margin: 0,
                                            fontFamily: 'Soleil,Arial,Sans-Serif',
                                        }}>Marketing Myth</p>
                                    </div>
                                    <div style={tabThree}>
                                        <IoIosPeople style={tabIcon} color={'#3E3E42'}  size={35} />
                                        <p style={tabTitle}>Offline Marketing</p>
                                    </div>
                        </div>
                    </div>
                </div>
            </div>
        )}
    }
}

